'use client';
import React, { useState, useMemo } from 'react';
import SongCard from './SongCard';
import SongModal from './SongModal';
import songsData from '../../data/songs.json';

type Song = {
  id: string;
  title: string;
  artist?: string;
  description?: string;
  imageUrl?: string;
  pdfUrl?: string;
  youtubeId?: string;
  lyrics?: string;
};

const ITEMS_PER_PAGE = 6;

export default function SongList() {
  const [selected, setSelected] = useState<Song | null>(null);
  const [open, setOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [currentPage, setCurrentPage] = useState(1);

  const openSong = (s: Song) => {
    setSelected(s);
    setOpen(true);
  };

  const close = () => {
    setOpen(false);
    setTimeout(() => setSelected(null), 200);
  };

  const handleSongSelect = (song: Song) => {
    setSelected(song);
  };

  // Filter songs based on search query
  const filteredSongs = useMemo(() => {
    const query = searchQuery.toLowerCase().trim();
    if (!query) return songsData as Song[];
    return (songsData as Song[]).filter(
      (song) =>
        song.title.toLowerCase().includes(query) ||
        (song.artist && song.artist.toLowerCase().includes(query)) ||
        (song.description && song.description.toLowerCase().includes(query))
    );
  }, [searchQuery]);

  // Pagination
  const totalPages = Math.ceil(filteredSongs.length / ITEMS_PER_PAGE);
  const paginatedSongs = useMemo(() => {
    const start = (currentPage - 1) * ITEMS_PER_PAGE;
    return filteredSongs.slice(start, start + ITEMS_PER_PAGE);
  }, [filteredSongs, currentPage]);

  // Reset to page 1 when search changes
  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchQuery(e.target.value);
    setCurrentPage(1);
  };

  return (
    <div className="py-8">
      <div className="container mx-auto">
        <h1 className="text-3xl font-bold mb-6 text-center">Worship Songs</h1>
        
        {/* Search Bar */}
        <div className="mb-8">
          <div className="relative max-w-md mx-auto">
            <input
              type="text"
              placeholder="Search songs by title, artist, or description..."
              value={searchQuery}
              onChange={handleSearchChange}
              className="input input-bordered w-full pl-12 pr-4 py-3 rounded-lg"
            />
            <svg
              className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              aria-hidden="true"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          </div>
          {searchQuery && (
            <p className="text-center text-sm text-gray-500 mt-2">
              Found {filteredSongs.length} song{filteredSongs.length !== 1 ? 's' : ''}
            </p>
          )}
        </div>

        {/* Songs Grid */}
        {paginatedSongs.length > 0 ? (
          <>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
              {paginatedSongs.map((s: Song) => (
                <SongCard key={s.id} song={s} onOpen={openSong} />
              ))}
            </div>

            {/* Pagination */}
            {totalPages > 1 && (
              <div className="flex justify-center items-center gap-2 mt-8">
                <button
                  className="btn btn-sm btn-outline"
                  onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                  disabled={currentPage === 1}
                >
                  Previous
                </button>
                <span className="px-4 text-sm">
                  Page {currentPage} of {totalPages}
                </span>
                <button
                  className="btn btn-sm btn-outline"
                  onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
                  disabled={currentPage === totalPages}
                >
                  Next
                </button>
              </div>
            )}
          </>
        ) : (
          <div className="text-center py-12">
            <p className="text-gray-500 text-lg">No songs found matching "{searchQuery}"</p>
            <button
              className="btn btn-link mt-4"
              onClick={() => setSearchQuery('')}
            >
              Clear search
            </button>
          </div>
        )}
      </div>

      <SongModal
        song={selected}
        open={open}
        onClose={close}
        allSongs={songsData as Song[]}
        onSongSelect={handleSongSelect}
      />
    </div>
  );
}

