'use client';
import React from 'react';
import Image from 'next/image';

type Song = {
  id: string;
  title: string;
  artist?: string;
  imageUrl?: string;
  pdfUrl?: string;
  youtubeId?: string;
  lyrics?: string;
};

export default function SongCard({ song, onOpen }: { song: Song; onOpen: (s: Song) => void }) {
  const copyLyrics = async (text?: string) => {
    if (!text) return;
    try {
      await navigator.clipboard.writeText(text);
      // small feedback could be added
    } catch (e) {
      console.error('copy failed', e);
    }
  };

  return (
    <div className="card card-compact w-full bg-base-100 shadow hover:shadow-lg transition">
      <figure>
        {song.imageUrl ? (
          <Image
            src={song.imageUrl}
            alt={`${song.title} cover`}
            width={400}
            height={240}
            className="object-cover w-full h-40 rounded-t"
          />
        ) : (
          <div className="h-40 w-full bg-base-300" />
        )}
      </figure>
      <div className="card-body">
        <h2 className="card-title">{song.title}</h2>
        <p className="text-sm text-muted">{song.artist}</p>
        <div className="card-actions justify-end mt-2">
          <button className="btn btn-sm btn-primary" onClick={() => onOpen(song)}>
            View
          </button>
          {song.pdfUrl && (
            <a className="btn btn-sm btn-outline" href={song.pdfUrl} target="_blank" rel="noreferrer">
              PDF
            </a>
          )}
          <button className="btn btn-sm btn-ghost" onClick={() => copyLyrics(song.lyrics)}>
            Copy Lyrics
          </button>
        </div>
      </div>
    </div>
  );
}
